  .table-item.-curr-item ~ * {
    transform: translate(-16%, -50%) scale(0.9);
    z-index: -1; }
  .table-item.-curr-item + * {
    right: 0;
    transform: translate(16%, -50%) scale(0.9);
    z-index: 1; }
  .table-item.-prev-item {
    transform: translate(-16%, -50%) scale(0.9); }
